<template>
  <full-page :options="options" id="fullpage" ref="fullpage">
    <div class="section">
      <img src="../../assets/img/nodenpm.png" width="1280"/>
    </div>
    <div class="section">
      <img src="../../assets/img/nodemain.png" width="1280"/>
    </div>
    <div class="section">
      <img src="../../assets/img/npm.png" width="1280"/>
      <router-link class="next" to="/conclusion">
        <img id="next" class="next" src="@/assets/img/okeyga.png" width="217.2"/>
      </router-link>
    </div>
  </full-page>
</template>

<script>

export default {
  name: 'App',
  data() {
    return {
      options: {
        onLeave: this.onLeave,
        afterLoad: this.afterLoad,
        afterRender: this.afterRender,
        afterResize: this.afterResize,
        afterReBuild: this.afterReBuild,
        afterResponsive: this.afterResponsive,
        afterSlideLoad: this.afterSlideLoad,
        onSlideLeave: this.onSlideLeave,
        navigation: true,
        scrollBar: false,
        fitToSectionDelay: 1000,
        anchors: ['main/1', 'main/2', 'main/3', 'main/4'],
        sectionsColor: ['#fff', '#83cd29', '#d1d0d5', '#ff8d8b']
      },
    }
  },
  created() {
    document.title = 'node и npm для простых смертных'
  },
  methods: {
    onLeave: function (origin, destination) {
      if (destination.anchor === 'main/3') {
        setTimeout(function () {
          document.getElementById('next').style.transform += 'translateX(-600px) '
        }, 2000);
      }
    }
  }
}
</script>
<style scoped>
.next {
  bottom: 0px;
  right: -560px;
  position: fixed;
  z-index: 99999;
  transition: transform ease-in-out 0.5s;
}
</style>
